/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
/* <--------------------------- * SECTION1 * --------------------------- */
.input {
    &_affix_wrapper {
        box-sizing: border-box;
        display: table-cell;
        padding: .4rem 1.1rem;
        overflow: hidden;
        border: .1rem solid lightgray;
        &__borderless {
            background-color: transparent;
            border: none;
            box-shadow: none;
        }
        &__focused {
            border: none;
            box-shadow: none;
        }

        &:hover,
        &:focus-within {
            position: relative;
            border: 0.1rem solid $primary_600;
            z-index: 99;
        }

        &:focus-within {
            box-shadow: 0 0 0 .2rem rgba($color: $primary_600, $alpha: .3);
        }

        input {
            line-height: 100%;
            padding: 0;
            border: none;
            outline: none;
        }
    }

    &_borderless {
        background-color: transparent;
        border: none;
        box-shadow: none;
    }

    &_suffix {
        display: inline-flex;
        flex: 1;
        align-items: center;
        margin-left: .4rem;
        *:not(:last-child) {
            margin-right: .8rem;
        }
    }

    &_clear_icon {
        font-size: 1.2rem;
        cursor: pointer;

        &__hidden {
            visibility: hidden;
        }
    }

    &_group_wrapper {
        display: inline-block;
    }

    &_wrapper {
        width: 100%;
    }

    &_group {
        position: relative;
        box-sizing: border-box;
        display: table;
        vertical-align: middle;
        border-collapse: separate;
        border-spacing: 0;
        margin: 0;
        padding: 0;
        height: 100%;
        &_addon {
            display: table-cell;
            box-sizing: border-box;
            background-color: #fafafa;
            border: .1rem solid #d9d9d9;
            padding: 0 1.1rem;
            &:nth-child(1) {
                border-top-left-radius: .2rem;
                border-bottom-left-radius: .2rem;
                border-right: none;
            }
            &:last-child {
                border-top-right-radius: .2rem;
                border-bottom-right-radius: .2rem;
                border-left: none;
            }
        }
    }

}
