/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@use "../../../Constant/breakPoint.scss" as *;
@use "../../../Constant/color.scss" as *;

/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin wrapper {
    width: 36.8rem;
    background-color: $background;
    box-shadow: $shadow_06dp;
    border-radius: 4px;
    outline: none;
    @media (max-width: $md) {
        width: 27.5rem;
    }

    [class*="kite_body"] {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */

/* <--------------------------- * SECTION1 * --------------------------- */

.datePicker_wrapper {
    height: 29.5rem;
    @include wrapper();
}

.dateTimePicker_wrapper {
    height: 35.5rem;
    @include wrapper();
}
